export default {
	data() {
		return {
			showCaseStage: false,
			showEmergency: false,
			caseStageData: [{
					value: 1,
					label: '未立案'
				},
				{
					value: 2,
					label: '法院审理阶段'
				},
				{
					value: 3,
					label: '执行阶段或已结案'
				}
			],
			emergencyData: [{
					value: 1,
					label: '紧急'
				},
				{
					value: 2,
					label: '特别紧急'
				}
			],
			caseDesc: '', // 案件描述
			customStyle: {
				'border-radius': '10px',
				'padding': '24rpx 18rpx',
				'line-height': '36rpx',
				'color': '#333333',
				'font-size': '28rpx'
			},
			uploadAction: 'https://t-xiaofa-lawyer.aegis-info.com/mall-im/api/common/uploadFile',
			uploadImg: [],
			caseStage: 1, // 案情阶段
			caseStageLabel: '',
			emergency: 1, // 紧急程度
			emergencyLabel: '',
			isrRadio: '1', // 是否同意
		};
	},
	mounted() {
		this.caseStageLabel = this.caseStageData.find(_x => _x.value === this.emergency).label;
		this.emergencyLabel = this.emergencyData.find(_x => _x.value === this.emergency).label
	},
	methods: {
		/**
		 * 图片上传成功
		 * @param {Object} data
		 * @param {Object} index
		 * @param {Object} lists
		 */
		handleSuccess(data, index, lists) {
			if (data && data.code === 200) {
				this.uploadImg.push(data.data);
			}
		},
		/**
		 * 移除图片
		 * @param {Object} index
		 * @param {Object} lists
		 */
		handleRemove(index, lists) {
			this.uploadImg.splice(index, 1);
		},
		/**
		 * 案情阶段确认
		 */
		handleConfirmCaseStage(index) {
			const {
				value,
				label
			} = this.caseStageData.find((_x, _i) => _i === index[0]);
			this.caseStage = value;
			this.caseStageLabel = label
		},
		/**
		 * 紧急程度确认
		 * @param {Object} index
		 */
		handleConfirmEmergency(index) {
			const {
				value,
				label
			} = this.emergencyData.find((_x, _i) => _i === index[0]);
			this.emergency = value;
			this.emergencyLabel = label
		},
		handleRadioChange(e) {
			console.log(e)
		}
	}
}
